<template>
<div class="stat__layout">
 <timer @change="getTime"></timer>
 <sale class="mt-20" :time-data="value"></sale>
</div>
</template>

<script>
 
import timer from './../components/time'
import sale from './sale'
export default {
   components: {
 
     sale,
     timer
   },
   data() {
     return {
       value: ['', '']
     }
   },
   methods: {
     getTime(time) {
       this.value = time
     }
   }
}
</script>

<style>
</style>



