<template>
  <div>
    <div class="stat__header">
      <div class="fl">商品类目分布
        <el-tooltip placement="right" effect="light">
          <div slot="content">转行率：付款人数 / 访客数
            <br>Tips: 所有统计数据均不排除售后服务商品
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24">
        <div class="table-box">
          <el-table :data="tableData" style="width: 100%" stripe ref="dataTable">
            <el-table-column label="序号" show-overflow-tooltip class-name="text-overflow">
              <template
                slot-scope="scope"
              >{{(queryParams.page-1)*queryParams.size+(scope.$index+1)}}</template>
            </el-table-column>
            <el-table-column
              prop="product.name"
              label="商品名称"
              show-overflow-tooltip
              class-name="text-overflow"
            ></el-table-column>
            <el-table-column label="浏览量" show-overflow-tooltip class-name="text-overflow">
              <template slot-scope="scope">{{scope.row.browseCount}}</template>
            </el-table-column>
            <el-table-column label="销售数量" show-overflow-tooltip class-name="text-overflow">
              <template slot-scope="scope">{{scope.row.saleCount}}</template>
            </el-table-column>
            <el-table-column label="销售金额" show-overflow-tooltip class-name="text-overflow">
              <template slot-scope="scope">¥{{$cms.float(scope.row.productPayAmount)}}</template>
            </el-table-column>
            <el-table-column prop="visitorsCount" label="访客数" align="center">
              <template slot-scope="scope">{{scope.row.visitorsCount}}</template>
            </el-table-column>
            <el-table-column prop="productPayCount" label="付款人数" align="center">
              <template slot-scope="scope">{{scope.row.productPayCount}}</template>
            </el-table-column>
            <el-table-column prop="convertionRate" label="转化率" align="center">
              <template slot-scope="scope">{{scope.row.convertionRate}}%</template>
            </el-table-column>
          </el-table>
          <cms-pagination @change="getPage" :total="total"></cms-pagination>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
const moment = require('moment')
import chartMixins from '../mixins.js'
import list from '@/mixins/list'
import { statisticsProductPage } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins, list],
  data() {
    return {
      newStatistics: {
        sumMemberCount: 0,
        sumNewMember: 0,
        sumRechargeMember: 0
      },
      queryParams: {
        page: 1,
        size: 10,
        startTime: moment()
          .subtract(7, 'days')
          .format('YYYY-MM-DD'),
        endTime: moment()
          .subtract(1, 'days')
          .format('YYYY-MM-DD')
      }
    }
  },
  methods: {
    getData() {
      this.getTableData(statisticsProductPage, this.queryParams)
    }
  }
}
</script>
<style lang="scss" scoped>
.table-box {
  width: 100%;
 
}
.stat__body{
  padding:20px
}
</style>



